import { Inject, Injectable } from '@angular/core';
import { environment } from '../../environments/environment';


@Injectable({
    providedIn: 'root'
  })
export class Config {

    private config: Object = null;

    constructor() {
        this.config = environment;
    }
    
    public getConfig(key: any) {
        return this.config[key];
    }    

}